.kiosk-finish-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 600;
}

.kiosk-finish-content {
    width: 1400px;
    height: 2000px;
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);

    display: grid;
    grid-template-rows: auto 800px auto;
    place-items: center;
}

.kiosk-finish-notice {
    grid-row: 1;
    font-size: 80px;
}

.kiosk-finish-scanner {
    grid-row: 2;
    width: 800px;
    font-size: 100px;
}

