/* 네비바 하단 고정 밑 세로 길이 설정 */
.nav-wrapper {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 6vh;
    background-color: #fff;
    padding: 10px 5px;
    margin-left: auto;
    margin-right: auto;
    z-index: 100;
    
  }
  

  /* nav태그 아래의 div태그들을 수평정렬 및 세로길이 설정 */
  .nav-link {
    /* 수평정렬, 3개의 button을 각각 33% width만큼 할당 */
    float: left;
    width: 33%;
    text-align: center;
  
    /* 세로길이 설정 */
    line-height: 55px;
  }
  
  /* 하단 네비바의 메뉴를 눌렀을 때 색상 변경*/
  .nav-item {
    color: rgba(128, 128, 128, 0.623);
    
  }
  
  /* 하단 네비바의 메뉴를 눌렀을 때 색상 변경*/
  .active-nav-item {
    color: black;
  }